      // },
      editItem(item) {
        this.editedIndex = this.drinks.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem(item) {
        const index = this.drinks.indexOf(item)
        confirm('Are you sure you want to delete this item?') && this.drinks.splice(index, 1)
      },

      edititem(item) {
        this.editedIndex = item['.key']
        this.cek = 0
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },
      deleteitem(item) {
        confirm('Are you sure you want to delete this item?') && drinksRef.child((item['.key'])).remove()
      },
      deleteMultiple() {
        for (var i = 0; i < this.selected.length; i++) {
          confirm('Are you sure you want to delete those item?') &&
            drinksRef.child((this.selected[i])).remove()
        }
      },

      close() {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },
      clear() {
        this.editedItem = {}
        this.editedItem.name = ''
        this.editedItem.calories = 0
